import React, { Suspense, lazy } from "react";
import { Navigate, RouteProps, Routes, Route, HashRouter, Link } from 'react-router-dom';
import Layout from "./js/react_modules/HOC/Layout/Layout";
import List from './js/react_modules/components/List/List';
import WordsConstructor from "./js/react_modules/containers/WordsConstructor/WordsConstructor";
import OppositeWords from './js/react_modules/containers/OppositeWords/OppositeWords';

import Auth from "./js/react_modules/containers/Auth/Auth";
import Logout from "./js/react_modules/containers/Auth/Logout/Logout";

import Rites from './js/react_modules/components/Texts/RitesOfPassage';
import Video from './js/react_modules/components/Video/Video';
import Button from './js/react_modules/components/UI/Button/Button';
import { videoData } from './js/constants';
import './styles/App.scss';

const SynonymsAsync = lazy(()=> import('./js/react_modules/containers/OppositeWords/OppositeWords'));
const FindMatchAsync = lazy(() => import('./js/react_modules/containers/FindMatch/FindMatch'));
const IdiomsAsync = lazy(() => import('./js/react_modules/containers/Idioms/Idioms'));
const DragAndDropAsync = lazy(() => import('./js/react_modules/containers/DragAndDrop/DragAndDrop'));
const FillGapsAsync = lazy(() => import('./js/react_modules/containers/FillGaps/FillGaps'));
const SentencesConstructorAsync = lazy(() => import('./js/react_modules/containers/SentencesConstructor/SentencesConstructor'));
const WordDefenitionMatchAsync = lazy(() => import('./js/react_modules/containers/WordDefenitionMatch/WordDefenitionMatch'));


const returnVideoChildren = (id) => {
  switch (id) {
    case 7:
      return (
        <div className="video-additional-content">
          <h2>Read the article and do the exercise below</h2>
          <a href="https://www.investopedia.com/ask/answers/032615/how-can-marginal-utility-explain-diamondwater-paradox.asp" className="mb-half">Why Are Diamonds Worth More Than Water?</a>
          <div className="btn-wrap">
            <Link to="/find_match/3:7">
              <Button classes="-primary -short">To task</Button>
            </Link>
          </div>
        </div>
      );
  }
};

const renderVideoRoutes = () => {
 return videoData.map((el) => (
    <Route path={el.path} element={<Video
      key={el.id}
      url={el.url}
      taskLink={el.taskLink}
      desc={el.title}
      children={returnVideoChildren(el.id)}
    />} />
  ));
};

const APP = () => {
  const routes = (
    <Routes>
      <Route path="/auth" element={<Auth />} />
      <Route path="/logout" element={<Logout />} />
      <Route path="/texts" element={<List content={[{desc: 'Rites of Passage', url: '/rites-of-passage'}]} />} />
      <Route path="/synonyms" element={<List content={[
        {desc: 'Unit 1', url: '/unit1'},
        {desc: 'Unit 2', url: '/unit2'},
        {desc: 'Unit 3', url: '/unit3'}
      ]} />} />
      <Route path="/word-defenition/:token" element={<WordDefenitionMatchAsync />} />
      <Route path='/videos' element={<List content={videoData.map((el) => ({
        desc: el.title,
        url: el.path,
      }))} />} />
      {renderVideoRoutes()}
      <Route path='/drag-and-drop/:token' element={<DragAndDropAsync />} />
      <Route path='/find_match/:token' element={<FindMatchAsync />} />
      <Route path='/idioms' element={<IdiomsAsync />} />
      <Route path="/logout" element={<Logout />} />
      <Route path='/rites-of-passage'element={<Rites />}/>
      <Route path="/rites-of-passage/task-1" element={<SynonymsAsync page='6'/>} />
      <Route path='/fill-gaps' element={<FillGapsAsync />} />
      <Route path="/unit1" element={<OppositeWords page='4' />} />
      <Route path="/unit2" element={<SynonymsAsync page='3' />} />
      <Route path="/unit3" element={<OppositeWords page='5' />} />
      <Route path="/opposite-words" element={<SynonymsAsync page='2'/>} />
      <Route path="/" element={<WordsConstructor />}  />
      {/* 
      <Route path="/sentences-constructor" element={<SentencesConstructorAsync page="16" />} />
      <Route path='/how-to-make-faster-decisions-task' element={<SynonymsAsync page='8' />} />
      <Route path='/fill-gaps' element={<FillGapsAsync />} /> */}
      {/* <Navigate to="/" /> */}
    </Routes>
  );

  return (
    <HashRouter>
      <Layout>
        <Suspense fallback={<div>Loading...</div>}>
          {routes}
        </Suspense>
      </Layout>
    </HashRouter>
  );
};

export default APP;
